import React, { useEffect, useState } from 'react'
import Table from 'react-bootstrap/Table'
import { useNavigate, useParams } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux';
import { DLT } from '../redux/actions/action';
import { ADD } from '../redux/actions/action';
import {REMOVE} from '../redux/actions/action'
function CardDetails() {






  const remove = (item)=>{
    dispatch(REMOVE(item))
  }
  const {id} = useParams();
  //console.log(id);
  const [data,setData] = useState([]);

  const dispatch = useDispatch();
  //add data.....
  const send =(e)=>{

    dispatch(ADD(e));
  }
  const dlt =(id)=>{
    dispatch(DLT(id))
    history('/');
  }
  const getdata = useSelector((state) => state.cartreducer.carts);
  console.log(getdata)
  
  const compare = ()=>{
    let comparedata = getdata.filter((e)=>{
      return e.id == id
    });
    setData(comparedata);
  }


  useEffect(()=>{
    compare();
  },[id])

const  history = useNavigate();


  return (
    <div>      <div className="container mt-2">
    <h2 className='text-center'>Iteams Details Page
    </h2>
    <section className='container mt-3'>
          <div className="iteamsdetails">
          {
            data.map((ele)=>{
              return (
                <>
                <div className="items_img">
              <img src={ele.image} alt="" />
            </div>

            <div className="details">
              <Table>
                <tr>
                  <td>
                    <p> <strong>Product</strong>  : {ele.title}</p>
                    <p> <strong>Price</strong>  : {ele.price}</p>
                    <p> <strong>Total</strong>  :  {ele.price * ele.qnty}</p>
                    <div className='mt-5 d-flex justify-content-between align-items-center' style={{width:100,cursor:"pointer",background:"#ddd",color:"#111"}}>
                    <span style={{fontSize:24}} onClick={ele.qnty <=1 ? ()=>dlt(ele.id) : ()=>remove(ele)}>-</span>
                    <span style={{fontSize:22}}>{ele.qnty}</span>
                    <span style={{fontSize:24}} onClick={()=>send(ele)}>+</span>

                    </div>

                  </td>
                  <td>
                    <p><strong>Rating :</strong> <span style={{background:"green",color:"#fff",padding:"2px 5px",borderRadius:"5px"}}>{ele.rating.rate} 	</span></p>
                    <p><strong>category :</strong> <span >{ele.category}	</span></p>
                    <p ><strong>Remove :</strong> <span ><i className='fas fa-trash' /* onClick={()=>dlt(ele.id)} */onClick={()=>dlt(ele.id)} style={{color:"red",fontSize:20,cursor:"pointer"}}></i>	</span></p>
                  </td>
                </tr>
                <p><b>about :- </b>{ele.description}</p>
              </Table>
            </div>
          
                </>
              )
            })
          }
          </div>
        </section>
  </div>
  </div>
  )
}

export default CardDetails